import * as Yup from 'yup';
import { useSnackbar } from 'notistack';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { CopyToClipboard } from 'react-copy-to-clipboard';
// @mui
import { styled } from '@mui/material/styles';
import { LoadingButton } from '@mui/lab';
import { Typography, Stack, Card, CardContent, Button, Box, Tooltip, IconButton } from '@mui/material';
import useAuth from '../../../../hooks/useAuth';
import Iconify from '../../../../components/Iconify';
import axios from '../../../../utils/axios';
// components
import { FormProvider, RHFTextField } from '../../../../components/hook-form';

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(({ theme }) => ({
  boxShadow: 'none',
  textAlign: 'center',
  backgroundColor: theme.palette.primary.lighter,
  [theme.breakpoints.up('md')]: {
    height: '100%',
    display: 'flex',
    textAlign: 'left',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
}));

export default function AppReferral() {
  const { setting, user } = useAuth();
  const { enqueueSnackbar } = useSnackbar();
  const ChangePassWordSchema = Yup.object().shape({
    refemail: Yup.string().email().required('Referral Email Required'),
  });
  const AccessToken = window.localStorage.getItem('accessToken');
  const defaultValues = {
    refemail: '',
  };
  const referralLink = `${window.location.origin}/auth/register/${user?.username}`;

  const methods = useForm({
    resolver: yupResolver(ChangePassWordSchema),
    defaultValues,
  });

  const {
    reset,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/inviting/user/${AccessToken}/secure`, data);
      reset()
      enqueueSnackbar('Invitation Sent Successfully!');
    } catch (error) {
      console.log(error)
      enqueueSnackbar('Failed to send invitation', { variant: 'error' })
    }
  };

  return (
    <RootStyle>
      <CardContent
        sx={{
          p: { md: 3 },
          width: 1,
          color: 'grey.800',
        }}
      >
        <Stack
          direction={{ xs: 'column', md: 'row' }}
          spacing={3}
          alignItems="center"
          justifyContent="space-between"
        >
          <Box sx={{ flexGrow: 1 }}>
            <Typography variant="h4" gutterBottom>
              Invite friends and earn {setting?.setting.referral_price}%
            </Typography>
            <Typography variant="body2" sx={{ mb: 3, opacity: 0.8 }}>
              Receive a {setting?.setting.referral_price}% bonus on your friend's first deposit.
            </Typography>

            <Stack direction="row" spacing={1} alignItems="center" sx={{ bgcolor: 'background.paper', p: 1, borderRadius: 1, border: '1px dashed', borderColor: 'primary.main' }}>
              <Typography variant="subtitle2" sx={{ wordBreak: 'break-all', flex: 1 }}>
                {referralLink}
              </Typography>
              <CopyToClipboard text={referralLink} onCopy={() => enqueueSnackbar('Referral link copied to clipboard!')}>
                <Tooltip title="Copy Link">
                  <IconButton color="primary">
                    <Iconify icon="eva:copy-fill" />
                  </IconButton>
                </Tooltip>
              </CopyToClipboard>
            </Stack>
          </Box>

          <Box sx={{ width: { xs: 1, md: 320 } }}>
            <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
              <Stack spacing={1.5}>
                <RHFTextField name="refemail" type="email" label="Referral Email" size="small" />
                <LoadingButton fullWidth type="submit" variant="contained" loading={isSubmitting}>
                  Send Invitation
                </LoadingButton>
              </Stack>
            </FormProvider>
          </Box>
        </Stack>
      </CardContent>
    </RootStyle>
  );
}
